"use client";

import { createContext, useContext, useState, useEffect } from "react";
import { collection, doc, getDocs, writeBatch } from "firebase/firestore";
import { db } from "@/app/lib/firebase";

type EventsContextType = {
  eventos: any[];
  colunas: string[];
  setFromExcel: (data: any[]) => void;
  salvarFirebase: () => Promise<void>;
  carregarFirebase: () => Promise<void>;
};

const EventsContext = createContext<EventsContextType | null>(null);

export function EventsProvider({ children }: { children: React.ReactNode }) {
  const [eventos, setEventos] = useState<any[]>([]);
  const [colunas, setColunas] = useState<string[]>([]);

  const setFromExcel = (data: any[]) => {
    setEventos(data);
    if (data.length > 0) {
      setColunas(Object.keys(data[0]));
    }
  };

const salvarFirebase = async () => {
  const batch = writeBatch(db);
  const ref = collection(db, "eventos");

  eventos.forEach(item => {
    const docRef = doc(ref);
    batch.set(docRef, item);
  });

  await batch.commit();
};

  const carregarFirebase = async () => {
    const ref = collection(db, "eventos");
    const snap = await getDocs(ref);
    const dados = snap.docs.map(doc => doc.data());
    setEventos(dados);
    if (dados.length > 0) {
      setColunas(Object.keys(dados[0]));
    }
  };

  useEffect(() => {
    carregarFirebase();
  }, []);

  return (
    <EventsContext.Provider value={{
      eventos,
      colunas,
      setFromExcel,
      salvarFirebase,
      carregarFirebase
    }}>
      {children}
    </EventsContext.Provider>
  );
}

export function useEvents() {
  const context = useContext(EventsContext);
  if (!context) {
    throw new Error("useEvents deve ser usado dentro do EventsProvider");
  }
  return context;
}
